.divider {
  padding: 0;
  border-width: 1px;
  border-style: none none solid none;
}

.divider-left {
  border-color: rgba(255, 255, 255, 0.3);
  position: relative;
  width: 90%;
  left: -45vw;
  text-align: right;
}

@media (min-width: 2051px) {
  .divider-left {
    visibility: hidden;
    opacity: 0;
  }
}

@media (min-width: 1800px) {
  .divider-left {
    width: 97%;
  }
}

.divider-left:before {
  content: '\f111';
  font-family: 'FontAwesome';
  font-size: 8px;
  line-height: 8px;
  position: absolute;
  top: 50%;
  left: 100%;
  margin-left: 12px;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.divider-left:after {
  content: '';
  width: 26px;
  height: 26px;
  border-radius: 100%;
  left: 100%;
  top: 50%;
  margin-left: 12px;
  transition: .3s;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  position: absolute;
}

.divider-img-shark {
  border-color: rgba(41, 45, 47, 0.1) !important;
}

.divider-img-shark:before {
  color: rgba(41, 45, 47, 0.3) !important;
}

.divider-img-shark:after {
  background: rgba(41, 45, 47, 0.04) !important;
}

.divider-img-brand {
  border-color: #1dc5fc !important;
}

.divider-img-brand:before {
  color: #1dc5fc !important;
}

.divider-img-brand:after {
  background: rgba(29, 197, 252, 0.4) !important;
}

.divider-left-white:before {
  color: #fff;
}

.divider-left-white:after {
  background: rgba(255, 255, 255, 0.1);
}

.divider-left-shark {
  border-color: rgba(41, 45, 47, 0.1);
}

.divider-left-shark:before {
  color: rgba(41, 45, 47, 0.3);
}

.divider-left-shark:after {
  background: rgba(41, 45, 47, 0.04);
}

.divider-img {
  position: absolute;
  width: 215px;
  right: -201px;
  text-align: right;
  top: 50%;
  display: none;
  border-color: rgba(255, 255, 255, 0.3);
}

