.thumb {
  position: relative;
  display: block;
  overflow: hidden;
}

.thumb > img {
  width: 100%;
}

.thumb__overlay {
  position: absolute;
  left: -1px;
  top: 0;
  bottom: 0;
  right: -1px;
  background: rgba(29, 197, 252, 0.97);
  opacity: 0;
  text-align: center;
  transition: .3s all ease;
}

.thumb__overlay > * {
  vertical-align: middle;
  -webkit-transform: translateY(30px);
  transform: translateY(30px);
  transition: .3s all ease;
}

.thumb-caption {
  text-transform: uppercase;
  font-family: "FuturaStd-Bold";
  font-size: 30px;
  line-height: 50px;
  letter-spacing: 0.14em;
  border: 4px solid white;
  position: absolute;
  padding: 0 15px;
  color: #fff;
  top: 50%;
  left: 50%;
  opacity: 0;
  transition: .3s;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

@media (min-width: 1200px) {
