  box-shadow: 3px 5px 30px 0px rgba(0, 0, 0, 0.3);
  visibility: hidden;
  opacity: 0;
  transition: 0.4s all ease-out;
  pointer-events: none;
}

.rd-timepicker.rd-timepicker--active {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, 0px);
  pointer-events: all;
}

.rd-timepicker:before {
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  content: '';
  position: absolute;
  border: 10px solid transparent;
  border-bottom: 10px solid #00796B;
}

.rd-timepicker-panel {
  background: #00796B;
  text-align: center;
  position: relative;
  height: 60px;
  padding: 0 50px;
}

.rd-timepicker-time {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 38px;
  line-height: 60px;
  color: #fff;
}

.rd-timepicker-time__hours, .rd-timepicker-time__minutes {
  opacity: 0.7;
  cursor: pointer;
  font-weight: 400;
  color: #fff;
}

.rd-timepicker .rd-timepicker-time--active {
  opacity: 1;
}

.rd-timepicker-meridiem {
  float: right;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.rd-timepicker-meridiem__item {
  display: block;
  line-height: 1.4;
  font-weight: 400;
  font-size: 16px;
  opacity: 0.7;
  cursor: pointer;
  color: #fff;
}

.rd-timepicker-wrapper {
  width: 275px;
  height: 275px;
  margin: 20px auto;
  border-radius: 50%;
  background: #eeeeee;
  position: relative;
}

.rd-timepicker-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 10px;
  height: 10px;
  background: #00796B;
  border-radius: 50%;
}

.rd-timepicker-line {
  position: absolute;
  background-color: #00796B;
  width: 2px;
  height: 125px;
  top: 136.5px;
  left: 136.5px;
  transform-origin: 1px 1px;
  transform: rotate(180deg);
}

.rd-timepicker-display {
  color: #3d3a3b;
  opacity: 0;
  transform: scale(1.2);
  visibility: hidden;
  transition: .4s ease;
  width: 100%;
  height: 100%;
  transform-origin: center center;
  position: absolute;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.rd-timepicker-display-inner li {
  position: absolute;
  text-align: center;
  border-radius: 50%;
  top: 117.5px;
  left: 117.5px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  cursor: pointer;
  overflow: hidden;
}

.rd-timepicker-display-inner li.rd-timepicker-display__item--active {
  background: #00796B;
  color: #fff;
}

.rd-timepicker-display-inner > *:nth-of-type(1) {
  transform: rotate(-60deg) translate(117.5px) rotate(60deg);
}

.rd-timepicker-display-inner > *:nth-of-type(2) {
  transform: rotate(-30deg) translate(117.5px) rotate(30deg);
}

.rd-timepicker-display-inner > *:nth-of-type(3) {
  transform: rotate(0deg) translate(117.5px) rotate(0deg);
}

.rd-timepicker-display-inner > *:nth-of-type(4) {
  transform: rotate(30deg) translate(117.5px) rotate(-30deg);
}

.rd-timepicker-display-inner > *:nth-of-type(5) {
  transform: rotate(60deg) translate(117.5px) rotate(-60deg);
}

.rd-timepicker-display-inner > *:nth-of-type(6) {
  transform: rotate(90deg) translate(117.5px) rotate(-90deg);
}

.rd-timepicker-display-inner > *:nth-of-type(7) {
  transform: rotate(120deg) translate(117.5px) rotate(-120deg);
}

.rd-timepicker-display-inner > *:nth-of-type(8) {
  transform: rotate(150deg) translate(117.5px) rotate(-150deg);
}

.rd-timepicker-display-inner > *:nth-of-type(9) {
  transform: rotate(180deg) translate(117.5px) rotate(-180deg);
}

.rd-timepicker-display-inner > *:nth-of-type(10) {
  transform: rotate(210deg) translate(117.5px) rotate(-210deg);
}

.rd-timepicker-display-inner > *:nth-of-type(11) {
  transform: rotate(240deg) translate(117.5px) rotate(-240deg);
}

.rd-timepicker-display-inner > *:nth-of-type(12) {
  transform: rotate(270deg) translate(117.5px) rotate(-270deg);
}

.rd-timepicker-display--active {
  opacity: 1;
  transform: scale(1);
  visibility: visible;
}

/**
 * RD Toggles
 * @Section
 */
label {
  cursor: pointer;
}

label span {
  display: inline-block;
}

.mfToggle__input-outer {
  content: "";
  display: inline-block;
  width: 30px;
  height: 15px;
  background-color: rgba(26, 57, 52, 0.7);
  border-radius: 15px;
  margin-right: 10px;
  transition: background 0.3s ease;
  vertical-align: middle;
  margin-left: 10px;
}

.mfToggle__input-outer:after {
  content: "";
  display: inline-block;
  width: 20px;
  height: 21px;
  background-color: #F1F1F1;
  border-radius: 20px;
  position: relative;
  box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.4);
  left: -9px;
  top: -4px;
  transition: left 0.3s ease, background 0.3s ease, box-shadow 0.1s ease;
}

.mfToggle__input-outer:active:after {
  box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.4), 0 0 0 15px rgba(32, 78, 70, 0.3);
}

.mfToggle__input-outer.active {
  background-color: rgba(11, 77, 70, 0.76);
}

.mfToggle__input-outer.active:after {
  background-color: #4ab19f;
  left: 11px;
}

.mfToggle__input-outer.active:active:after {
  box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.4), 0 0 0 15px rgba(27, 105, 74, 0.35);
}

.mfToggle__input-outer input[type="checkbox"] {
  position: absolute !important;
  margin: 0 !important;
  padding: 0 !important;
  top: 0 !important;
  left: 0 !important;
  height: 0 !important;
  width: 0 !important;
  border: none !important;
  overflow: hidden !important;
  opacity: 0 !important;
  z-index: -999999 !important;
}

.mfCheckbox__input-outer {
  position: relative;
  width: 20px;
  height: 20px;
  display: inline-block;
  border: 2px solid;
  margin-left: 10px;
  border-radius: 2px;
  overflow: hidden;
  vertical-align: text-bottom;
}

.mfCheckbox__input-outer:before {
  position: absolute;
  content: "";
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  display: block;
  margin-top: -4px;
  margin-left: 6px;
  width: 0;
  height: 0;
  -webkit-animation: checkbox-off 0.3s linear forwards;
  animation: checkbox-off 0.3s linear forwards;
}

.mfCheckbox__input-outer:after {
  display: block;
  position: absolute;
  content: "";
  background-color: rgba(0, 0, 0, 0.84);
  left: -5px;
  top: -15px;
  height: 50px;
  width: 50px;
  border-radius: 100%;
  z-index: 1;
  opacity: 0;
  margin: 0;
}

.mfCheckbox__input-outer.active:before {
  box-shadow: 0 0 0 10px, 10px -10px 0 10px, 32px 0px 0 20px, 0px 32px 0 20px, -5px 5px 0 10px, 20px -12px 0 11px;
  -webkit-animation: checkbox-on 0.3s linear forwards;
  animation: checkbox-on 0.3s linear forwards;
}

.mfCheckbox__input-outer input[type="checkbox"] {
  position: absolute !important;
  margin: 0 !important;
  padding: 0 !important;
  top: 0 !important;
  left: 0 !important;
  height: 0 !important;
  width: 0 !important;
  border: none !important;
  overflow: hidden !important;
  opacity: 0 !important;
  z-index: -999999 !important;
}

.mfRadio__input-outer {
  vertical-align: middle;
  position: relative;
  display: inline-block;
  width: 18px;
  height: 18px;
  margin-left: 12px;
  border: 2px solid #333;
  border-radius: 100%;
  transition: 0.1s ease;
}

.mfRadio__input-outer:after {
  content: '';
  position: absolute;
  top: 3px;
  left: 3px;
  right: 3px;
  bottom: 3px;
  background-color: transparent;
  border-radius: 100%;
  transition: background 0.3s ease;
}

.mfRadio__input-outer:active {
  box-shadow: 0 1px 3px 1px rgba(220, 220, 220, 0.4), 0 0 0 15px rgba(133, 148, 171, 0.52);
}

.mfRadio__input-outer:hover:after {
  background-color: rgba(0, 0, 0, 0.4);
}

.mfRadio__input-outer.active:after {
  background-color: #333;
}

.mfRadio__input-outer.active:active {
  box-shadow: none;
}

.mfRadio input[type="radio"] {
  position: absolute !important;
  margin: 0 !important;
  padding: 0 !important;
  top: 0 !important;
  left: 0 !important;
  height: 0 !important;
  width: 0 !important;
  border: none !important;
  overflow: hidden !important;
  opacity: 0 !important;
  z-index: -999999 !important;
}

.mfRadio span {
  vertical-align: middle;
}

