//
// Custom typography
// --------------------------------------------------

// Page header
// -------------------------

.page-header {
}

// Fonts
// -------------------------

.font-default {
  font-family: $font-family-base;
}


// Basic Typography
// -------------------------

#{headings()}{
  margin-top: 0;
  margin-bottom: 0;
}

h1,
.h1 {
  font-size: 45px;
  line-height: 1.2;
  font-family: $font-family-cursive;
  text-transform: uppercase;
  @media (min-width: $screen-xs-min) and (max-width: $screen-sm-min - 1) {
    font-size: 70px;
  }
  @media (min-width: $screen-sm-min) and (max-width: $screen-md-min - 1) {
    font-size: 100px;
  }
  @media (min-width: $screen-md-min) and (max-width: $screen-lg-min - 1) {
    font-size: 150px;
    line-height: 1.2;
  }
  @media (min-width: $screen-lg-min) {
    line-height: 0.85;
    font-size: 150px;
  }
}

h2,
.h2 {
  font-size: 42px;
  line-height: 2;
  text-transform: uppercase;
  font-family: $font-family-sec;
  letter-spacing: 0.04em;
  color:$shark;
  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h2;
    font-size: $font-size-h2;
  }
  @media (min-width: $screen-md-min) and (max-width: $screen-lg-min - 1) {
    line-height: 1.3;
  }
}

h3,
.h3 {
  font-size: 20px;
  line-height: 1.5;
  font-weight: 300;
  letter-spacing: -0.4px;
  @media (min-width: $screen-md-min) and (max-width: $screen-lg-min - 1) {
    font-size: 24px;
    line-height: 1.2;
  }
  @media (min-width: $screen-lg-min) {
    line-height: $font-lh-h3;
    font-size: $font-size-h3;
  }
}

h4,
.h4 {
  font-size: 24px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h4;
    font-size: $font-size-h4;
  }
}

h5,
.h5 {
  font-size: 20px;
  line-height: 1.5;

  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h5;
    font-size: $font-size-h5;
  }
}

h6,
.h6 {
  font-size: 15px;
  line-height: 1.2;
  font-family: $font-family-base;
  text-transform: uppercase;
  font-weight: 300;
  letter-spacing: .24em;
  @media (min-width: $screen-sm-min) {
    line-height: $font-lh-h6;
    font-size: $font-size-h6;
  }
}
p{
  font-weight: 300;
  //letter-spacing: -0.2px;
}
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  a {
    transition: .3s all ease;
  }

  a:hover {
    color: $brand-primary;
  }

  .small {
    color: $gray-dark;
  }
}
a:focus{
  outline: none;
}
p,
.p,
.list {
  a:hover {
    text-decoration: underline;
  }
}

@media (min-width: $screen-xs) {
  .big {
    font-size: 106.25%;
    line-height: 1.470588235294118;

    h1 &,
    .h1 & {
      font-size: 142.22%;
      line-height: 1.125;
    }

    h2 &,
    .h2 & {
      font-size: 147.36%;
      line-height: 1.142857142857143;
    }
  }
}

blockquote small,
blockquote .small,
small,
.small {
  font-size: 93.75%;

  h1 &,
  .h1 & {
    font-size: 62.22222222222222%;
  }

  h2 &,
  .h2 & {
    font-size: 63.15789473684211%;
  }

  h3 &,
  .h3 & {
    font-size: 73.33333333333333%;
  }

  h4 &,
  .h4 & {
    font-size: 75%;
  }

  h5 &,
  .h5 & {
    font-size: 72.72727272727273%;
  }

  h6 &,
  .h6 & {
    font-size: 94.11764705882353%;
  }
}

// Lists
// -------------------------

ul,
ol {
  padding: 0;
  margin: 0;

}
//
// list-terms
//
.list-terms {
  @media (min-width: $screen-lg-min) {
    margin-top: 75px;
  }
  margin-top: 25px;
  margin-bottom: 25px;
  > dt + dd {
    margin-top: 15px;
  }
  dd{
    font-weight: 300;
    line-height: 30px;
    font-family: $font-family-base;
  }
  dt{
    font-size: 28px;
    line-height: 1;
    font-weight: 300;
    color: $brand-primary;
    letter-spacing: -0.018em;
  }
  > dd + dt {
    margin-top: 33px;
  }
}
.mail{
  display: inline-block;
  a{
    font-weight: 300;
    transition: .3s;
    line-height: 1;
    display: inline-block;
    &:hover{
      transition: .3s;
      color: $shark;
    }
  }
}

//
// list-number
//

.list-number, .list{
  list-style-type:decimal;
  text-align: left;
  font-size: 20px;
  @media (min-width: $screen-md-min) {
    font-size: 28px;
  }
  color: $brand-primary;
  font-weight: 300;
  position: relative;
  padding-left: 30px;
  @media (min-width: $screen-md-min) {
    padding-left: 127px;
    margin-top: -5px;
  }
  &:before{
    content: '';
    position: absolute;
    width: 1px;
    height: 115%;
    background-color:#e8e9e9;
    top: -30px;
    left: -15px;
    display: none;
    @media (min-width: $screen-md-min) {
      display: block;
    }
  }
  li+li{
    margin-top: 10px;
  }
  li{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 85px;
      height: 1px;
      background-color:#e8e9e9;
      top: 50%;
      left: -142px;
      display: none;
      @media (min-width: $screen-md-min) {
        display: block;
      }
    }
    >a{
      margin-left: 10px;
      transition: .3s;
      &:hover{
        transition: .3s;
        color:$shark;
      }
    }
  }
}

.list {
  list-style: none;
  @media (min-width: $screen-sm-min) {
    padding-left: 92px;
  }
  padding-left: 0;
  &:before{
    @media (min-width: $screen-lg-min) {
      height: 170%;
    }
    height: 120%;
  }
  > li + li {
    margin-top: 10px;
  }
  li{
    &:before{
      left: -107px;
    }
  }
  a{
    &:hover{
      text-decoration: none;
    }
  }
}


// List inline
//
.list-inline {
  margin-left: -5px;
  margin-right: -5px;

  > li {
    padding-left: 5px;
    padding-right: 5px;
  }
}

.list-inline-sm {
  margin-left: -10px;
  margin-right: -10px;

  > li {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.list-inline-md {
  margin-left: -15px;
  margin-right: -15px;

  > li {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.list-inline-lg {
  margin-left: -30px;
  margin-right: -30px;

  > li {
    padding-left: 30px;
    padding-right: 30px;
  }
}


// List index
//
.list-index {
  @include list-unstyled;
  counter-reset: li;
  li {
    .list-index-counter {
      &:before {
        content: counter(li, decimal-leading-zero);
        counter-increment: li;
      }
    }
  }
}


// Backgrounds
// -------------------------
.bg-primary{
  position: relative;
  &:before{
    content:'';
    background-color: $brand-primary;
    left: 0;
    top: 0;
    bottom: 0;
    right: 50%;
  }
  h2{
    color: $white;
  }
}
.bg-alabaster{
  position: relative;
  &:before{
    content:'';
    background-color: $alabaster;
    left: 0;
    top: -400px;
    bottom: -400px;
    right: 50%;
    position: absolute;
    z-index: -1;
    @media (max-width: $screen-md-min - 1) {
      display: none;

    }
  }
}
.bg-alabaster-var-2{
  position: relative;
  &:before{
    content:'';
    background-color: $alabaster;
    left: 50%;
    top: -400px;
    bottom: 70px;
    right: 0;
    position: absolute;
    z-index: -2;
    @media (min-width: $screen-lg-min) {
      bottom: 174px;
    }
    @media (max-width: $screen-md-min - 1) {
      display: none;
    }
  }
}
