//
// divider
//
.divider{
  padding: 0;
  border-width: 1px;
  border-style: none none solid none;
}
.divider-left{
  @media (min-width: 2051px) {
    visibility: hidden;
    opacity: 0;
  }
  border-color: rgba($white, 0.3);
  position: relative;
  width: 90%;
  left: -45vw;
  text-align: right;
  @media (min-width: $screen-xl-min) {
    width: 97%;
  }
  &:before{
    content:'\f111';
    font-family: 'FontAwesome';
    font-size: 8px;
    line-height: 8px;
    position: absolute;
    top: 50%;
    left: 100%;
    margin-left: 12px;
    @include transform(translate(-50%,-50%));
  }
  &:after{
    content: '';
    width: 26px;
    height: 26px;
    border-radius: 100%;
    left: 100%;
    top: 50%;
    margin-left: 12px;
    transition: .3s;
    @include transform(translate(-50%,-50%));
    position: absolute;
  }
}
.divider-img-shark{
  border-color: rgba($shark, 0.1)!important;
  &:before{
    color: rgba($shark,.3)!important;
  }
  &:after{
    background: rgba($shark,.04)!important;
  }
}
.divider-img-brand{
  border-color: $brand-primary!important;
  &:before{
    color: $brand-primary!important;
  }
  &:after{
    background: rgba($brand-primary,.4)!important;
  }
}
.divider-left-white{
  &:before{
    color: $white;
  }
  &:after{
    background: rgba($white,.1);
  }
}
.divider-left-shark{
  border-color: rgba($shark, 0.1);
  &:before{
    color: rgba($shark,.3);
  }
  &:after{
    background: rgba($shark,.04);
  }
}
.divider-img{
  position: absolute;
  width: 215px;
  right: -201px;
  text-align: right;
  top: 50%;
  display: none;
  border-color: rgba($white, 0.3);
