/*
* @subsection   Thumb
*/

.thumb {
  position: relative;
  display: block;
  overflow: hidden;

  > img {
    width: 100%;
  }

  &__overlay {
    position: absolute;
    left: -1px;
    top: 0;
    bottom: 0;
    right: -1px;
    background: rgba($brand-primary, .97);
    opacity: 0;
    text-align: center;
    transition: .3s all ease;

    > *{
      vertical-align: middle;
      @include transform(translateY(30px));
      transition: .3s all ease;
    }
  }
  &-caption{
    text-transform: uppercase;
    font-family: $font-family-sec;
    font-size: 30px;
    line-height: 50px;
    letter-spacing: 0.14em;
    border: 4px solid white;
    position: absolute;
    @media (min-width: $screen-lg-min) {
      padding: 0 20px 0 34px;
    }
    padding: 0 15px;
    color: $white;
    top: 50%;
    left: 50%;
    opacity: 0;
    transition: .3s;
    @include transform(translate(-50%,-50%));
    .brand-slogan{
      color: #91e4ff;
      padding-left: 15px;
    }
  }
  &:hover {
    .thumb__overlay {
      opacity: 1;

      > *{
        @include transform(translateY(0));
      }
    }
    .thumb-caption{
      opacity: 1;
    }
  }
}
