.post{
  .post-meta{
    color: $brand-primary;
    font-weight: 300;
    margin-top: 30px;
    span{
      @media (max-width: $screen-xs-min - 1) {
        display: block;
      }
      font-size: 14px;
      &:before{
        font-size: 14px;
      }
      time{
