      }
      span.p{
        display: inline-block;
        font-size: 14px;
        padding-left: 9px;
      }
    }
  }
}


//
// @search
//
.rd-navbar-search {
  position: relative;
  .rd-navbar-search-form {
    label {
      display: inline-block;
      input {
        @include placeholder($white);
        padding-left: 12px;
        padding-right: 12px;
        font-weight: 300;
        @media (min-width: $screen-lg-min) {
          min-width: 270px;
        }
        width: 175px;
        @media (min-width: $screen-xs-min) {
          width: 250px;
        }
        height: 68px;
        color: white;
        background-color: #69d9fd;
        border: 0 solid transparent;
        outline: none;
      }
    }
    .rd-navbar-search-form-submit {
      display: inline-block;
      position: relative;
      width: 100px;
      @media (min-width: $screen-lg-min) {
        width: 170px;
      }
      @media  (max-width: $screen-lg-min - 1) {
        margin-left: 18px;
      }
      height: 68px;
      line-height: 48px;
      border: 4px solid $white;
      cursor: pointer;
      color: #fff;
      text-transform: uppercase;
      text-align: center;
      font-size: 16px;
      font-family: $font-family-sec;
      background: transparent;
      @media (min-width: $screen-lg-min) {
        float: right;
      }
      outline: none;
      &:before{
        display: none;
      }
      &:hover{
        color: $shark;
        border-color:$shark;
      }
    }
  }
}
.rd-navbar-live-search-results{
  -webkit-box-shadow: 5px 5px 5px 0 rgba(0,0,0,0.25)!important;
  -moz-box-shadow: 5px 5px 5px 0 rgba(0,0,0,0.25)!important;
  box-shadow: 5px 5px 5px 0 rgba(0,0,0,0.25)!important;
  @include transform(translateY(0%))
}
