//
// Imports
//

$assets-futurabook-path: "../../../assets/fonts/FuturaBook/";
$assets-futurabold-path: "../../../assets/fonts/FuturaBold/";
$assets-futuracondensed-path: "../../../assets/fonts/FuturaCondensed/";
$assets-futuralight-path: "../../../assets/fonts/FuturaLight/";

@font-face {
  font-family: 'FuturaStd-Book';
  src: url('#{$assets-futurabook-path}FuturaStd-Book.eot?#iefix') format('embedded-opentype'),  url('#{$assets-futurabook-path}FuturaStd-Book.otf')  format('opentype'),
	     url('#{$assets-futurabook-path}FuturaStd-Book.woff') format('woff'), url('#{$assets-futurabook-path}FuturaStd-Book.ttf')  format('truetype'), url('#{$assets-futurabook-path}FuturaStd-Book.svg#FuturaStd-Book') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'FuturaStd-Bold';
  src: url('#{$assets-futurabold-path}FuturaStd-Bold.eot?#iefix') format('embedded-opentype'),  url('#{$assets-futurabold-path}FuturaStd-Bold.otf')  format('opentype'),
	     url('#{$assets-futurabold-path}FuturaStd-Bold.woff') format('woff'), url('#{$assets-futurabold-path}FuturaStd-Bold.ttf')  format('truetype'), url('#{$assets-futurabold-path}FuturaStd-Bold.svg#FuturaStd-Bold') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'FuturaStd-CondensedBold';
  src: url('#{$assets-futuracondensed-path}FuturaStd-CondensedBold.eot?#iefix') format('embedded-opentype'),  url('#{$assets-futuracondensed-path}FuturaStd-CondensedBold.otf')  format('opentype'),
	     url('#{$assets-futuracondensed-path}FuturaStd-CondensedBold.woff') format('woff'), url('#{$assets-futuracondensed-path}FuturaStd-CondensedBold.ttf')  format('truetype'), url('#{$assets-futuracondensed-path}FuturaStd-CondensedBold.svg#FuturaStd-CondensedBold') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'FuturaStd-Light';
  src: url('#{$assets-futuralight-path}FuturaStd-Light.eot?#iefix') format('embedded-opentype'),  url('#{$assets-futuralight-path}FuturaStd-Light.otf')  format('opentype'),
       url('#{$assets-futuralight-path}FuturaStd-Light.woff') format('woff'), url('#{$assets-futuralight-path}FuturaStd-Light.ttf')  format('truetype'), url('#{$assets-futuralight-path}FuturaStd-Light.svg#FuturaStd-Light') format('svg');
  font-weight: normal;
  font-style: normal;
}

@import "variables";
@import "toolkit";
@import "custom";